.SearchBox__main  {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--bgcolor-b2);
    border-radius: 8px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    padding: 2rem 0;
    margin-bottom: 1rem;
  }
  
  /* ---------------------------------------------------------------------- */
  /* ---------------------------------------------------------------------- */
  /* ---------------------------------------------------------------------- */
  /* -------------------------- SEACH_BOX_ICON ---------------------------- */
  
  .SearchBox__LogoAndr7st {
    height: 120px;
    pointer-events: none;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }    
  @media (prefers-reduced-motion: no-preference) {
    .SearchBox__LogoAndr7st {
      animation: App-logo-spin infinite 40s linear;
    }
  }
  
  /* ---------------------------------------------------------------------- */
  /* ---------------------------------------------------------------------- */
  /* ---------------------------------------------------------------------- */
  
  .SearchBox__Andr7st_name {
    color: azure;
    font-size: 1rem;
  }
  
  
  .SearchBox__search_label > input {
    width: 10rem;
  }
  
  .SearchBox__search_label > button {
    margin-left: .1rem;
  }