.RepoBox__Root {
  border-radius: .4rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin-bottom: .4rem;
}
.RepoBox__Title {
  text-align: left;
  /* horizontal aligment*/
  display:flex;
  justify-content: left;
  align-items: center;
  margin-bottom: 1rem;
}
.RepoBox__TitleImg {
  padding: .4rem;
  width:  4rem;
  height: 4rem;
}
.RepoBox__TitleText {
  margin-left: 1rem;
  font-size: 1.4rem;
  color: blueviolet;
}

.RepoBox__description {
  border-color: rgb(46, 0, 0);
  text-align: left;
  /*padding: .1px .4rem;*/
  border-radius: .4rem;
  margin: 0 .4rem;
  background: var(--bgc-description);
  padding: .2rem .4rem;
  margin-bottom: 1rem;


}
.RepoBox__description > p {
  color: rgb(22, 3, 3);
}

